import BaseApiService from "./BaseApiService";

const DEFAULT_PATH = "/custom/api/document";
const COMMON_PATH = "/custom/api";

class DocumentService extends BaseApiService {
  
  getDocument = (uid: string) => {
    const endPoint = DEFAULT_PATH + `/manage/uid/${uid}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // -------------------------- To get existing documents --------------------------------- //
  getDocumentList = (start = 0, limit = 10, search: string) => {
    const endPoint =
      COMMON_PATH + `/loan/document/section/search?start=${start}&limit=${limit}&${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // -------------------------- To get existing documents --------------------------------- //
  getKycDocumentList = (start = 0, limit = 10, search: string) => {
    const endPoint = COMMON_PATH + `/kyc/document/search?start=${start}&limit=${limit}&${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };
}

export default new DocumentService();
